import { Component, OnChanges, Input, SimpleChanges, Output, EventEmitter, ViewChild, ElementRef } from '@angular/core';
import { CdkDragMove, CdkDragDrop } from '@angular/cdk/drag-drop';

import { IRoom } from '../../../services/data/rooms.service';
import { IBooking } from '../../../services/data/bookings.service';
import { IEventOverlap } from './event/event.component';
import { BaseComponent } from '../../../shared/globals/base.component';
import { AppService } from '../../../services/app.service';

import * as dayjs from 'dayjs';

@Component({
    selector: 'day-view-space',
    templateUrl: './space.component.html',
    styleUrls: ['./space.component.scss']
})
export class DayViewSpaceComponent extends BaseComponent implements OnChanges {
    /** List of space dropzones connected to */
    @Input() public space_list: string[];
    /** Date to display events for */
    @Input() public date: number;
    /** Space to display events for */
    @Input() public space: IRoom;
    /** Scroll value of the day view */
    @Input() public scroll: number;
    /** Scroll value to add to drop placeholders */
    @Input('scrollTop') public scroll_top: number;
    /** Last update time of the displayed bookings */
    @Input('lastUpdate') public last_update: number;
    /** Emitter for whether the space column is in view */
    @Output() public inView = new EventEmitter<boolean>();
    /** Emitter for whether an event is being dragged */
    @Output() public dragging = new EventEmitter<boolean>();
    /** Mapping of event IDs to overlap details */
    public overlap_details: { [id: string]: IEventOverlap } = {};
    /** Whether to only show meetings with a setup or breakdown time */
    public overflow_only: boolean;
    /** Mapping of whether to show events with a given legend id */
    public legend: { [id: string]: boolean } = {};
    /** Mapping of positions of event in the day */
    public position_list: { [id: string]: { top: number, height: number } } = {};
    /** Override for mapped position */
    public top: number;
    /** Client Rectangle for the container element box */
    public box: ClientRect;
    /** Whether the space list is in view */
    public in_view: boolean;
    /** ID of the booking with an action processing */
    public override_state: string;
    /** Settings for day view */
    public settings: { [key: string]: any } = {};

    /** Container element for the component contents */
    @ViewChild('view', { static: true }) private view: ElementRef<HTMLDivElement>;

    constructor(private _service: AppService) {
        super();
    }

    /** Events to display for the active date */
    public get events(): IBooking[] {
        const date = dayjs(this.date).format('DD MMM YYYY');
        if (this.space) {
            const bookings = this.space.timeline[date] || [];
            const list = bookings.filter(i => {
                if (this.override_state && this.override_state === i.icaluid) {
                    i.state = 'loading';
                }
                return i.state !== 'cancelled'
                    ? (!this.overflow_only || i.setup || i.breakdown) && this.legend[i.booking_type] !== false
                    : (!this.overflow_only || i.setup || i.breakdown) && this.legend.declined !== false
            });
            return list;
        }
        return [];
    }

    public ngOnInit(): void {
        if (!this._service.ready()) {
            return this.timeout('init', () => this.ngOnInit());
        }
        this.settings = this._service.Settings.get('app.day_view') || {};
        this.subscription('breakdown', this._service.listen('APP.breakdown', (state) => {
            this.overflow_only = state;
        }));
        this.subscription('legend', this._service.listen('CONCIERGE.legend', (state) => {
            this.legend = state || {};
        }));
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.scroll) {
            this.checkInView();
        }
        if (changes.last_update) {
            this.timeout('update_overlaps', () => this.calculateOverlaps(), 1000);
        }
    }

    /**
     * Track by function for ngFor optimisation
     * @param index Index of the item
     * @param item Data associated with the item at the given index
     */
    public trackByFn(index: number, item: IBooking) {
        return item.id || index;
    }

    /**
     * Calculate to overlap details for the booking events on the space
     */
    private calculateOverlaps() {
        const events = this.events.sort((a, b) => a.date - b.date || a.duration - b.duration || (a.title || '').localeCompare(b.title || ''));
        events.forEach(i => this.overlap_details[i.id] = { index: 0, total: 1 });
        for (const bkn of events) {
            const bkn_start = dayjs(bkn.date).startOf('m');
            const bkn_end = dayjs(bkn_start).add(bkn.duration, 'm').startOf('m');
            let count = 1;
            let index = 0;
            const collisions: IBooking[] = [];
            for (const cmp of events) {
                if (bkn.id !== cmp.id) {
                    const cmp_start = dayjs(cmp.date).startOf('m');
                    const cmp_end = dayjs(cmp_start).add(cmp.duration, 'm').startOf('m');
                    if ((bkn_start.isSame(cmp_start, 'm') || (bkn_start.isAfter(cmp_start, 'm') && bkn_start.isBefore(cmp_end, 'm'))) ||
                        (bkn_end.isSame(cmp_end, 'm') || (bkn_end.isAfter(cmp_start, 'm') && bkn_end.isBefore(cmp_end, 'm')))) {
                        count++;
                        collisions.push(bkn);
                        if (events.findIndex(i => i.id === bkn.id) > events.findIndex(i => i.id === cmp.id)) {
                            index++;
                        }
                    }
                }
            }
            // collisions.forEach(i => this.overlap_details[i.id].total = count > this.overlap_details[i.id].total ? count : this.overlap_details[i.id].total);
            // const indexes = collisions.reduce((a, v) => {
            //     if (this.overlap_details[v.id].index || this.overlap_details[v.id].index === 0) {
            //         a.push(this.overlap_details[v.id].index);
            //     }
            //     return a;
            // }, []);
            // if (indexes.length > 0) {
            //     for (let n = 0; n < count; n++) {
            //         if (!indexes.find(i => i === n)) { index = n; }
            //     }
            // }
            this.overlap_details[bkn.id] = { index: Math.max(0, index), total: count };
        }
    }

    /**
     * Check whether the space column is with the view of the
     */
    private checkInView() {
        if (this.view && this.view.nativeElement) {
            const el = this.view.nativeElement;
            this.in_view = el.offsetLeft + this.scroll >= -240 && el.offsetLeft + this.scroll <= window.innerWidth;
            this.inView.emit(this.in_view);
        }
    }

    /**
     * Initialise drag events
     */
    public initDrag() {
        this.box = this.view.nativeElement.getBoundingClientRect();
        console.log('Drag Start');
        this.dragging.emit(true);
    }

    /**
     * Cleanup after drag
     */
    public endDrag() {
        this.timeout('end_drag', () => this.top = 0, 10);
        console.log('Drag End');
        this.dragging.emit(false);
    }

    /**
     * Update top position of the dragged event placeholder
     * @param booking Booking being dragged
     * @param event Drag event
     */
    public updateDragTop(event: CdkDragMove<IBooking>) {
        if (event.delta.y) {
            const y = event.pointerPosition.y;
            const top = (y - this.box.top - this.scroll_top) / this.box.height;
            const top_minutes = (Math.round((top * 24 * 60) / 15) * 15) / 60;
            this.top = (top_minutes / 24) * 100;
        }
    }

    /**
     * Handle drop event
     * @param event
     */
    public drop(event: CdkDragDrop<{ top: number; list: IBooking[]}>) {
        const id = event.item.element.nativeElement.id;
        const booking = event.previousContainer.data.list.find(i => i.id === id);
        if (!booking) {
            return;
        }
        const old_state = booking.state;
        const top = event.previousContainer.data.top;
        const new_start = Math.round(((top || 0) / 100) * 24 * 60);
        const old_date = dayjs(booking.date);
        const date = old_date.startOf('d').minute(new_start);
        const now = dayjs().startOf('m');
        // Prevent moving event into past
        if (now.isAfter(date, 'm')) {
            return this._service.error('Meetings cannot be moved into the past');
        }
        // Check for collisions
        booking.state = 'loading';
        this.override_state = booking.icaluid;
        let room = booking.room;
        let message = `Update ${booking.organiser.name}'s meeting in "${(booking.room || ({} as any)).name}" from ${old_date.format('h:mm A')} to ${date.format('h:mm A')}`;
        let success = `Successfully updated meeting time from ${old_date.format('h:mm A')} to ${date.format('h:mm A')}`;
        let new_booking: any = { ...booking, old_date: booking.date, date: date.valueOf() };
        if (event.container !== event.previousContainer) { // Same room
            room = this._service.Rooms.item(event.container.id.replace('space-col-', ''));
            new_booking.from_room = booking.room.email;
            message = `Move ${booking.organiser.name}'s meeting in "${(booking.room || ({} as any)).name}" to "${room.name}" at ${date.format('h:mm A')}`;
            success = `Successfully moved booking to "${room.name}" at ${date.format('h:mm A')}`;
        }
        // Check if booking has a change
        if (room === booking.room && date.isSame(old_date, 'm')) {
            booking.state = old_state;
            this.override_state = null;
            return;
        }
        const room_list = [room, ...booking.room_list.filter(i => i.id !== booking.room.id)];
        // Check if booking is already in the room
        const room_count = room_list.reduce((a, v) => a.add(v.id), new Set()).size;
        if (room_count < room_list.length) {
            booking.state = old_state;
            this.override_state = null;
            return this._service.error('The meeting is already in this room');
        }
        new_booking.room = room_list;
        this.checkCollisions(room_list, date.valueOf(), booking.duration, booking.icaluid).then(() => {
            this.moveBooking(message, new_booking).then((bkn) => {
                this._service.Rooms.removeFromTimeline(booking.room.id, booking.id);
                this._service.Rooms.addToTimeline(bkn);
                this.override_state = null;
                this._service.success(success);
            }, (err) => {
                booking.state = old_state;
                this.override_state = null;
                if (err !== 'User cancelled') {
                    this._service.error(`Error: ${err}`);
                }
            });
        }, () => {
            booking.state = old_state;
            this.override_state = null;
            this._service.error('Updated meeting time clashes with another');
        });
    }

    /**
     * Check if there are any booking collisions with the new booking time
     * @param spaces
     * @param date
     * @param duration
     */
    private checkCollisions(spaces: IRoom[], date: number, duration: number, ignore?: string) {
        return new Promise((resolve, reject) => {
            this._service.Rooms.available({
                room_ids: spaces.map(i => i.id).join(','),
                date,
                duration,
                ignore,
                hide_bookings: true
            }).then((list) => {
                list.length === spaces.length ? resolve() : reject();
            }, () => reject());
        });
    }

    /**
     * Update booking
     * @param confirm_msg
     * @param new_booking
     */
    private moveBooking(confirm_msg: string, new_booking: { [key: string]: any }): Promise<IBooking> {
        return new Promise((resolve, reject) => {
            this._service.confirm({
                title: 'Update meeting',
                message: confirm_msg,
                icon: 'event_available',
                accept: 'Ok',
                cancel: true
            }, (event) => {
                if (event.type === 'Accept') {
                    this._service.Bookings.updateItem(new_booking.id, new_booking)
                        .then((bkn: IBooking) => resolve(bkn), (err) => reject(err));
                } else {
                    reject('User cancelled');
                }
                event.close();
            })
        });
    }
}
